@import url(https://fonts.googleapis.com/css2?family=Merienda+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.cart-meal-name {
  font-size: 1.4rem;
}

.cart-group-left-col {
  margin-top: 15px;
}

.cart-group-middle-col {
  margin-top: 15px;
}

.cart-group-right-col {
  margin-top: 40px;
  margin-right: 10px;
}

.cart-quantity-value {
  border-radius: 8px;
  margin-top: 20px;
  background-color: white;
  border-color: #a1a5a688;
  width: 70px;
  color: black;
}

.cart-button-group {
  border-radius: 8px;
  background-color: white;
  border-color: #a1a5a688;
  width: 100px;
  color: black;
  margin-top: 10px;
  margin-left: 5px;
}

.cart-line {
  height: 2px;
  background: #b5bbbe;
  margin-bottom: 5px;
}
.modal-body {
  padding-bottom: 20px;
  font-family: 'Merienda One', cursive;
}

.modal-list {
  max-height: 20rem;
  overflow: scroll;
  overflow-x: hidden;
}

.modal-title {
  font-size: 2.5rem;
  font-weight: 300;
}

.modal-total-price {
  font-size: 30px;
  margin-top: 10px;
  margin-bottom: 20px;
}

.cart-buttons button {
  margin-left: 15px;
  width: 120px;
  height: 40px;
  border-radius: 8px;
}

.close {
  background-color: #6a7073;
  color: white;
}

.order-success {
  height: 200px;
}

.order-success p {
  color: green;
  font-size: 3rem;
}

.error {
  color: rgba(255, 0, 0, 0.76);
  font-size: 1rem;
}

.http-error {
  color: rgba(255, 0, 0, 0.76);
  font-size: 2.5rem;
  margin-top: 20px;
  text-align: center;
};
.header {
  position: relative;
  height: 120px;
  background-color: #a1a5a688;
  z-index: 10;
}

.header-header {
  color: white;
}

.header-link {
  background-color: #353d40e3;
  border-radius: 20px;
  width: 300px;
  text-align: center;
}

.header-cart-logo {
  color: #F2B138;
  margin-bottom: 10px;
}

.header-cart-text {
  display: inline-block;
  margin-left: 20px;
  color: #F2B138;
}

.header-cart-quantity {
  display: inline-block;
  border-radius: 20px;
  margin-left: 20px;
  width: 60px;
  height: 30px;
  color: #F2B138;
  font-size: 1.5rem;
  background-color: #a1a5a688;
}

.header-image {
  width: 100%;
  height: 25rem;
  z-index: 0;
  overflow: hidden;
}

.header-image img {
  width: 110%;
  height: 100%;
  object-fit: cover;
  -webkit-transform: rotateZ(-5deg) translateY(-4rem) translateX(-1rem);
          transform: rotateZ(-5deg) translateY(-4rem) translateX(-1rem);
}
.meal-container {
  font-family: 'Merienda One', cursive;
  width: 100%;
  z-index: 20;
}

.meal-name {
  font-size: 1.4rem;
  margin-bottom: 3px;
  margin-top: 3px;
}

.meal-description {
  font-size: 0.9rem;
}

.meal-price {
  font-size: 1.2rem;
}

.meal-amount {
  margin-right: 15px;
}

.meal-input {
  width: 50px;
  border-radius: 4px;
  margin-bottom: 5px;
  margin-top: 2px;
}

.meal-line {
  height: 1px;
  background: #b5bbbe;
}

.meal-add-button {
  height: 30px;
  width: 100px;
  font-size: 15px;
  background-color: #6a7073;
  border-radius: 5px;
  margin-top: 5px;
  color: white;
}

.meal-group-right {
  margin-right: 20px;
}
.card {
  padding: 1rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  border-radius: 14px;
  background-color: rgba(255, 255, 255, 0.925);
  width: 70%;
  margin: auto;
}

.summary {
  
  text-align: center;
  max-width: 45rem;
  width: 90%;
  margin: auto;
  margin-top: -10rem;
  margin-bottom: 3rem;
  position: relative;
  background-color: #383838;
  color: white;
  border-radius: 14px;
  padding: 1rem;
  box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25);
}

.summary h2 {
  font-size: 2rem;
  margin-top: 0;
}

.App {
  background-color: #383838;
}
